/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}
@media (max-width: 768px) {
  html {
    font-size: 50%;
  }
}
@media (max-width: 480px) {
  html {
    font-size: 46%;
  }
}

body {
  min-height: 100%;
  background-color: #f9f9f9;
  color: #000000;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

*, *:before, *:after {
  box-sizing: border-box;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  width: 85%;
  max-width: 1100px;
  margin: 0 auto;
}

.section {
  margin: 5rem auto;
  margin-top: 12rem;
}
@media (max-width: 768px) {
  .section {
    margin-top: 5rem;
  }
}

a {
  text-decoration: none;
}

ul, li {
  list-style: none;
}

img {
  max-width: 100%;
  width: inherit;
  height: inherit;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 0;
  font-weight: 900;
  color: #000000;
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.6rem;
}

h3 {
  font-size: 2rem;
}

p {
  color: #000000;
  font-size: 2.6rem;
}

.alert {
  display: block;
  margin: 1rem auto;
  padding: 1rem;
  color: #FFFFFF;
  font-weight: 900;
  font-size: 1rem;
  text-transform: uppercase;
  max-width: fit-content;
  border-radius: 1rem;
}
.alert.error {
  background-color: #d11919;
  border-left: 0.5rem solid #a31414;
}
.alert.success {
  background-color: #50a845;
  border-left: 0.5rem solid #3f8436;
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.content {
  margin: auto;
  width: 330px;
  padding: 40px 30px;
  background: #dde1e7;
  border-radius: 10px;
  box-shadow: -3px -3px 7px rgba(255, 255, 255, 0.4509803922), 2px 2px 5px rgba(94, 104, 121, 0.288);
}

.content .text {
  font-size: 33px;
  font-weight: 600;
  margin-bottom: 35px;
  color: #595959;
}

.content .text h3 {
  font-size: 28px; /* Tamaño de fuente deseado */
  color: rgba(42, 45, 51, 0.288); /* Color deseado */
  font-weight: bold; /* Grosor de fuente deseado */
  margin-bottom: 5px; /* Espacio inferior deseado */
}

.field {
  height: 50px;
  width: 100%;
  display: flex;
  position: relative;
  margin-top: 20px;
}

.field:nth-child(2) {
  margin-top: 20px;
}

.field input {
  height: 100%;
  width: 100%;
  padding-left: 45px;
  outline: none;
  border: none;
  font-size: 18px;
  background: #dde1e7;
  color: #595959;
  border-radius: 25px;
  box-shadow: inset 2px 2px 5px #BABECC, inset -5px -5px 10px rgba(255, 255, 255, 0.4509803922);
}

.field input:focus {
  box-shadow: inset 1px 1px 2px #BABECC, inset -1px -1px 2px rgba(255, 255, 255, 0.4509803922);
}

.field span {
  position: absolute;
  color: #595959;
  width: 50px;
  line-height: 50px;
}

.field label {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 45px;
  pointer-events: none;
  color: #666666;
}

.field input:valid ~ label {
  opacity: 0;
}

.button {
  margin-top: 20px;
  width: 100%;
  height: 50px;
  font-size: 18px;
  line-height: 50px;
  font-weight: 600;
  background: #dde1e7;
  border-radius: 25px;
  border: none;
  outline: none;
  cursor: pointer;
  color: #595959;
  box-shadow: 2px 2px 5px #BABECC, -5px -5px 10px rgba(255, 255, 255, 0.4509803922);
}

.button:focus {
  color: #3498db;
  box-shadow: inset 2px 2px 5px #BABECC, inset -5px -5px 10px rgba(255, 255, 255, 0.4509803922);
}